.tile {
  font-size: 1.5em;
  line-height: 3em;
  height: 3em;
  text-align: center;
  width: 3em;
  -webkit-box-shadow: 2px 0 0 0 #888, 0 2px 0 0 #888, 2px 2px 0 0 #888,
    2px 0 0 0 #888 inset, 0 2px 0 0 #888 inset;
          box-shadow: 2px 0 0 0 #888, 0 2px 0 0 #888, 2px 2px 0 0 #888,
    2px 0 0 0 #888 inset, 0 2px 0 0 #888 inset;
}

.Bot {
  background-color: #c2b6b6;
}

.Human {
  background-color: #bdf;
}

.Empty {
  cursor: pointer;
}



